  min-height: 24px;
}
.phn-no1 {
  position: relative;
  line-height: 24px;
}
.output-formatter {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: 0 var(--padding-9xl) 0 0;
  gap: var(--gap-base);
}
.mail-icon1 {
  height: 24px;
  width: 24px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  min-height: 24px;
}
.emailgmailcom1 {
