  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-37xl);
}
.edge-node {
  align-self: stretch;
  overflow: hidden;
  align-items: center;
}
.b-tree-node {
  position: absolute;
  top: 7368px;
  left: 15px;
  width: 345px;
  align-items: flex-start;
  text-align: left;
